<template>
  <div>
    <van-sticky>
      <van-nav-bar title="充值" left-arrow @click-left="onClickLeft" />
    </van-sticky>

    <div class="userWrap">
      <!-- <van-field label="验证码" placeholder="请输入验证码" v-model="userInfo.pid_member"  /> -->
      <van-cell-group>
        <van-field label="充值积分数量" placeholder="请输入充值积分数量" v-model="userInfo.integral" @input="getUsdt" />
        <van-field label="对应USDT数量"  v-model="userInfo.usdt" disabled />
        <van-field label="充值金额"  v-model="userInfo.money" disabled />
        <van-field label="TXID" placeholder="请输入TXID" v-model="userInfo.txid" />
        <!-- <van-field label="提现金额" placeholder="请确认新密码" v-model="userInfo.pid_member"  />
        -->
        <van-cell title="平台收款码" />
        <div>
          <img
            :src="img"
            @click="showPicFullscreen(img)"
            alt
            style="width:80px;height:80px;margin-left:20px;"
          />
        </div>
        <van-cell title="上传充值凭证" />

        <van-uploader :after-read="upImg" accept="image/*">
          <van-image :src="userInfo.voucher" class="userImg" round v-if="userInfo.voucher"></van-image>
        </van-uploader>
      </van-cell-group>
      <button class="submitBtn" @click="updateUser">提交</button>
    </div>
       <van-dialog
      v-model="showPassword"
      title="交易密码"
      show-cancel-button
      @confirm="pay"
      @cancel="cancelPay"
      :before-close="beforePasswordClose"
    >
      <van-password-input :value="password" info="密码为 6 位数字类型" style="position:initial" />
    </van-dialog>
    <van-number-keyboard
      id="keyboard-panel"
      :show="showPassword"
      @input="onInput"
      @delete="onDelete"
      :z-index="9999"
      style="color:black"
    />
  </div>
</template>

<script>
import {
  rechargeInfo,
  uploadImg,
  updateUser,
  rechargeIntegralList
} from "@/api/personal";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
         week:{},
          showPassword:false,
      password:"",
      userInfo: {
        integral: "",
        usdt: "",
        transaction:"",
        txid:'',
        money: "",
        voucher: ""
      },
      img: ""
    };
  },
  created() {
    this.getUser();
  },
  methods: {
            beforePasswordClose(action, done) {
      if (action === "confirm") {
        if (this.password.length != 6) {
          done(false);
        } else {
          done();
        }
      } else {
        done();
      }
    },
    onInput(key) {
      this.password = (this.password + key).slice(0, 6);
      this.zIndex = 9999;
    },
    onDelete() {
      this.password = this.password.slice(0, this.password.length - 1);
    },
    cancelPay() {
      this.password = "";
    },
    pay() {
        this.userInfo.transaction=this.password
     rechargeIntegralList(this.userInfo).then(res => {
          if(res.errcode==0){
          this.$toast(res.msg);
          setTimeout(res=>{
              this.$router.go(-1);
          },200)
          }else{
          this.$toast(res.msg);
          }
      });
    },
      getUsdt(){
          this.userInfo.usdt=this.cal.accMul(this.userInfo.integral,this.week[0].config_value)
          this.userInfo.money=this.cal.accMul(this.userInfo.usdt,this.week[1].config_value)
      },
    upImg(file) {
      this.image = file.content.substring(file.content.indexOf(",") + 1);
      uploadImg(this.image, "").then(resp => {
        this.userInfo.voucher = resp.img;
      });
    },
    showPicFullscreen(picUrl) {
      ImagePreview([picUrl]);
    },
    getUser() {
      rechargeInfo().then(res => {
        this.week=res
        this.img = res[2].config_value;
      });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    // upImg(file) {
    //   this.image = file.content.substring(file.content.indexOf(",") + 1);
    //   uploadImg(this.image, "").then(resp => {
    //     this.userInfo.wechat_img = resp.img;
    //   });
    // },
    // 提交
    updateUser() {
        if(this.userInfo.integral==''){
          this.$toast('请输入充值积分数量');
          return false

        }else if(this.userInfo.txid==''){
          this.$toast('请输入TXID');
          return false

        }else if(this.userInfo.voucher==''){
          this.$toast('请上传充值凭证');
          return false
        }else{
             this.showPassword=true
        }
    
    }
  }
};
</script>
<style lang="scss" scoped>
/deep/ .van-dialog{
    top:35%;position: fixed;
    .van-dialog__header{
        height: 45px;
    }
    .van-password-input__security{
        margin-bottom: 10px;
    }
    .van-password-input__info{
        padding-bottom: 20px;
    }
}
.userImg {
  width: 39px;
  height: 39px;
}
/deep/ .van-uploader {
  left: 20px;
}
/deep/ .van-field__label {
  width: 120px;
}
.van-button--primary {
  background: white;
  color: #28a6ff;
  border: 1px solid #28a6ff;
  border-radius: 20px;
}
.van-cell {
  padding: 15px 16px 19px;
}
.userImg {
  width: 39px;
  height: 39px;
}
.submitBtn {
  display: block;
  width: 345px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
  background: #28a6ff;
  margin: 66px auto;
  border: 0 none;
}
</style>
